import { fetchNews } from './api';

let cachedNews = null;

const getNewsList = async () => {
  if (!cachedNews) {
    try {
      const data = await fetchNews();
      cachedNews = data.feed || [];
    } catch (error) {
      console.error('   :', error);
      return [];
    }
  }
  return cachedNews;
};

const getNewsById = async (id) => {
  const newsList = await getNewsList();
  //   id  
  const stringId = String(id);
  return newsList.find(item => String(item.id) === stringId);
};

const setNewsCache = (news) => {
  cachedNews = news;
};

export { getNewsList, getNewsById, setNewsCache };
